import * as React from 'react';
import { WebView } from 'react-native-webview';
const html = '<!DOCTYPE html><html><head>' +
    '<script type="text/javascript" async="" src="https://statics.qa.chatnels.com/admin/js/chatnels.client.js"></script>' +
    '<style>#chatnels-root {width: 100%; height: 100%; position: relative;}</style>' +
    '</head><body><div id="chatnels-root" /></body></html>';
const jsTemplate = (orgDomain, sessionToken) => `window.chatnelsAsync = function() {
            window.ChatnelsClient.load({ domain: ${orgDomain}, sessionToken: ${sessionToken} });
            window.ChatnelsClient.on('chatnels:message', handleChatnelsMessage);
        };`;
export const ChatnelsWidget = ({ orgDomain, serviceProvider, sessionToken, }) => {
    const webviewRef = React.useRef(null);
    const [js, setJs] = React.useState();
    React.useEffect(() => {
        if (orgDomain) {
            setJs(jsTemplate(orgDomain, sessionToken));
        }
    }, []);
    return (React.createElement(WebView, { ref: webviewRef, source: { html }, injectedJavaScript: js, 
        // onMessage={(event) => {}}
        style: { flex: 1 } }));
};
